/* base 三种尺寸 */
@media screen and (max-width: 1680px) {
  .tv_live_floor > .title {
    font-size: 18px;
    color: #323232;
    margin: 20px 0 10px 0;
    position: relative;
  }
  .tv_live_floor > .title > .left {
    font-size: 18px;
  }
  .tv_live_floor > .title > .right {
    font-size: 14px;
    color: #666;
    position: absolute;
    bottom: 0;
    right: 0;
    vertical-align: middle;
    cursor: pointer;
  }
  .tv_live_floor > .title > .right > i {
    width: 16px;
    height: 16px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 4px;
  }
  .tv_live_floor > .title > .right .def {
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
  }
  .tv_live_floor > .title > .right:hover {
    color: #007AFF;
  }
  .tv_live_floor > .title > .right:hover .def {
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
    transition: all 1s ease 0s;
  }
  .tv_live_floor .base-top-img-bottom-text-pop-v .topImgBottomText_V {
    width: 890px;
    margin: 0 auto;
    padding: 0;
    font-size: 22px;
  }
  .tv_live_floor .base-top-img-bottom-text-pop-v .topImgBottomText_V ul li {
    width: 135px;
    margin-right: 10px;
  }
  .tv_live_floor .base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .topImg img {
    width: 135px;
    height: 186px;
  }
  .tv_live_floor .base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .topImg .score {
    color: #e0a111;
  }
  .tv_live_floor .base-top-img-bottom-text-pop-v .topImgBottomText_V ul li:nth-child(6n + 6) {
    margin-right: 0px;
  }
}
