.main-container {
  max-height: 280px;
  overflow-y: auto;
}
.pdl10 {
  padding-left: 10px;
}
.icon_history_empty {
  width: 126px;
  height: 114px;
  background: url(data:image/png;base64,);
  margin-left: auto;
  margin-right: auto;
  margin-top: 80px;
}
.play-history-wrap {
  width: 280px;
  font-size: 12px;
  color: #333333;
  border-top: 4px solid #007aff;
  line-height: 16px;
}
.play-history-wrap .list-container {
  padding-right: 10px;
  padding-left: 15px;
  padding-top: 26px;
  padding-bottom: 16px;
}
.play-history-wrap .empty-container {
  width: 100%;
  height: 336px;
}
.play-history-wrap .tri {
  border-width: 8px;
  border-color: transparent transparent #007aff transparent;
  border-style: solid;
  left: 50%;
  top: -18px;
  margin-left: -8px;
  width: 0;
  height: 0;
  position: absolute;
}
.play-history-wrap .icon-switch {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid #c6c6c6;
  float: left;
  position: relative;
  top: 2px;
}
.play-history-wrap .icon-switch.active {
  border: 1px solid #007aff;
  background: #007aff;
}
.play-history-wrap .op-wrap {
  height: 16px;
  vertical-align: middle;
  top: 10px;
  right: 10px;
  z-index: 2;
}
.play-history-wrap .l {
  width: 11px;
  height: 11px;
  float: left;
  background: url(data:image/png;base64,);
  position: relative;
  top: 2px;
  z-index: 2;
}
.play-history-wrap .line {
  left: 5px;
  width: 1px;
  height: 100%;
  position: absolute;
  background: #f3f3f3;
}
.play-history-wrap .r {
  margin-left: 1px;
}
.play-history-wrap .t {
  font-size: 14px;
  padding-left: 15px;
  color: #007aff;
}
.play-history-wrap .item {
  margin-top: 10px;
}
.play-history-wrap .sub-item {
  line-height: 20px;
  vertical-align: middle;
  margin-top: 4px;
  margin-left: 5px;
  cursor: pointer;
}
.play-history-wrap .sub-item .hover-info {
  background: #e9e9e9;
  padding-top: 2px;
  padding-bottom: 2px;
  line-height: 16px;
  display: none;
}
.play-history-wrap .sub-item:hover .normal-info {
  display: none;
}
.play-history-wrap .sub-item:hover .hover-info {
  display: block;
}
.play-history-wrap .sub-item .wc65 {
  width: 65%;
}
.play-history-wrap .sub-item .mwc24 {
  max-width: 26%;
}
.play-history-wrap .sub-item .icon-status {
  margin-left: auto;
  margin-right: auto;
  width: 12px;
  height: 12px;
  top: 4px;
  position: relative;
  margin-right: 4px;
}
