  height: 12px;
  top: 2px;
  position: relative;
  margin-right: 4px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAMCAYAAAEhcmxxAAAABGdBTUEAALGPC/xhBQAAAI1JREFUKBWtUTESgCAME1cfBN/Q2d/p7jOABzkjqZejIAt3MmhN0sTSadInhHC/3zHG1aAEZIzZtaxIpIK2onPPQeBDAig5lA2/tS/z5IdJpJRO59xSOSOYAhAzHgCp1CS4Hw+s2/weJlPoXO/9llcgV4pVWGsvzVcNcMTo3BnrtkkMcHG9iZEITqcM1Q/qtmGD1rC3SwAAAABJRU5ErkJggg==);
