.play-history-wrap .btn {
  border-radius: 12px;
  width: 100px;
  height: 24px;
  line-height: 24px;
  color: #ffffff;
  background: #007aff;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  vertical-align: middle;
}
