/* base 三种尺寸 */
.titles {
  width: 100%;
  height: 60px;
  padding-bottom: 11px;
  position: relative;
}
.titles .leftName {
  display: inline;
  position: absolute;
  bottom: 11px;
  cursor: pointer;
}
.titles .leftName:hover {
  display: inline;
  position: absolute;
  bottom: 11px;
  color: #007AFF!important;
}
.titles .rightName {
  display: inline;
  position: absolute;
  bottom: 11px;
  cursor: pointer;
}
.titles .rightName > span {
  display: inline-block;
  vertical-align: middle;
}
.titles .rightName e {
  display: inline-block;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  margin-left: 8px;
  vertical-align: middle;
}
@media screen and (max-width: 1680px) {
  .indexTitles {
    width: 1160px;
    padding: 0 20px;
    margin: 0 auto;
    font-size: 22px;
  }
  .indexTitles .leftName {
    font-weight: 700;
    cursor: pointer;
  }
  .indexTitles .rightName {
    font-size: 16px;
    cursor: pointer;
  }
  .indexTitles .rightName e {
    width: 12px;
    height: 16px;
  }
  .detailTitle {
    width: 100%;
    padding: 0;
    margin: 0 auto;
    font-size: 18px;
    color: #323232;
  }
  .detailTitle .title .rightName {
    color: #666;
    font-size: 12px;
  }
  .detailTitle .title .rightName e {
    width: 12px;
    height: 12px;
  }
}
@media screen and (min-width: 1680px) {
  .indexTitles {
    width: 1640px;
    margin: 0 auto;
    padding: 0 20px;
    font-size: 22px;
  }
  .indexTitles .leftName {
    font-weight: 700;
  }
  .indexTitles .rightName {
    font-size: 16px;
  }
  .indexTitles .rightName e {
    width: 12px;
    height: 16px;
  }
  .detailTitle {
    width: 100%;
    padding: 0;
    margin: 0 auto;
    font-size: 18px;
    color: #323232;
  }
  .detailTitle .title .rightName {
    color: #666;
    font-size: 12px;
  }
  .detailTitle .title .rightName e {
    width: 12px;
    height: 12px;
  }
}
