/* base 三种尺寸 */
.ccComWbZan {
  background-image: url(data:image/png;base64,);
}
.ccComZan {
  background-image: url(data:image/png;base64,);
}
.ccComWbReply {
  background-image: url(data:image/png;base64,);
}
.ccComReply {
  background-image: url(data:image/png;base64,);
}
.ccComActive {
  color: #007AFF !important;
}
.ccComNo {
  color: #B2B2B2 !important;
}
textarea::-webkit-input-placeholder {
  color: #aab2bd;
  font-size: 16px;
}
@media screen and (max-width: 1680px) {
  .com-comment {
    margin-top: 30px;
  }
  .com-comment .title {
    margin-bottom: 14px;
  }
  .com-comment .title span {
    display: inline-block;
    text-align: left;
  }
  .com-comment .title span:nth-child(1) {
    font-size: 22px;
    color: #333;
  }
  .com-comment .title span:nth-child(2) {
    font-size: 16px;
    color: #B2B2B2;
  }
  .com-comment .info {
    margin-bottom: 30px;
    padding-left: 10px;
    box-sizing: border-box;
    font-size: 0;
  }
  .com-comment .info > div {
    display: inline-block;
  }
  .com-comment .info .avatar {
    width: 40px;
    height: 90px;
    margin-right: 10px;
    position: relative;
  }
  .com-comment .info .avatar img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  .com-comment .info .content {
    width: 798px;
    height: 88px;
    border: 1px solid #E5E5E5;
    position: relative;
  }
  .com-comment .info .content textarea {
    width: 708px;
    height: 88px;
    outline: medium;
    font-size: 16px;
    color: #333;
    padding: 10px;
    box-sizing: border-box;
  }
  .com-comment .info .content .btn {
    width: 90px;
    height: 88px;
    position: absolute;
    top: 0;
    right: 0;
    background-image: linear-gradient(-225deg, #4BAFFF 0%, #3657FF 100%);
    font-size: 18px;
    color: #FFF;
    line-height: 88px;
    text-align: center;
    cursor: pointer;
  }
  .com-comment .all-comment {
    font-size: 18px;
    color: #333;
    margin-bottom: 10px;
  }
  .com-comment .line {
    width: 860px;
    height: 1px;
    background: #E6E6E6;
    margin-bottom: 10px;
  }
  .com-comment .user-info {
    width: 860px;
  }
  .com-comment .user-info ul > li {
    padding: 20px 0px 0px 10px;
    box-sizing: border-box;
  }
  .com-comment .user-info ul > li .master {
    overflow: hidden;
  }
  .com-comment .user-info ul > li .master .photo {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    float: left;
  }
  .com-comment .user-info ul > li .master .photo img {
    width: 40px;
    border-radius: 50%;
  }
  .com-comment .user-info ul > li .master .txt {
    width: 800px;
    float: right;
  }
  .com-comment .user-info ul > li .master .txt p:nth-child(1) {
    font-size: 14px;
    color: #333;
  }
  .com-comment .user-info ul > li .master .txt p:nth-child(1) span {
    font-size: 12px;
    color: #B2B2B2;
    margin-left: 10px;
  }
  .com-comment .user-info ul > li .master .txt p:nth-child(2) {
    font-size: 14px;
    color: #666666;
  }
  .com-comment .user-info ul > li .master .txt .other-reply {
    background: #F2F2F2;
    margin-top: 10px;
    padding: 20px;
    box-sizing: border-box;
  }
  .com-comment .user-info ul > li .master .txt .other-reply ul > li {
    padding: 0;
    margin-bottom: 10px;
  }
  .com-comment .user-info ul > li .master .txt .other-reply ul > li span {
    display: inline-block;
    font-size: 14px;
  }
  .com-comment .user-info ul > li .master .txt .other-reply ul > li span:nth-child(1) {
    color: #333;
  }
  .com-comment .user-info ul > li .master .txt .other-reply ul > li span:nth-child(2) {
    color: #666;
  }
  .com-comment .user-info ul > li .master .txt .other-reply ul .look-all {
    margin-top: 20px;
  }
  .com-comment .user-info ul > li .master .txt .other-reply ul .look-all p {
    max-width: 200px;
    font-size: 14px;
    color: #B2B2B2;
    cursor: pointer;
  }
  .com-comment .user-info ul > li .master .txt .other-reply ul .look-all p i {
    font-style: normal;
  }
  .com-comment .user-info ul > li .master .txt .extra-function {
    width: 800px;
    height: 20px;
    line-height: 20px;
    margin: 10px 0;
  }
  .com-comment .user-info ul > li .master .txt .extra-function > div {
    display: inline-block;
    vertical-align: middle;
  }
  .com-comment .user-info ul > li .master .txt .extra-function > div i {
    font-style: normal;
    display: inline-block;
    height: 20px;
    line-height: 20px;
    vertical-align: bottom;
    font-size: 12px;
    color: #CCC;
  }
  .com-comment .user-info ul > li .master .txt .extra-function .zan {
    margin-right: 16px;
    font-size: 12px;
  }
  .com-comment .user-info ul > li .master .txt .extra-function .zan span i {
    cursor: pointer;
  }
  .com-comment .user-info ul > li .master .txt .extra-function .zan span i:nth-child(1) {
    width: 20px;
    background-size: 100% 100%;
  }
  .com-comment .user-info ul > li .master .txt .extra-function .zan span:nth-child(2) {
    height: 20px;
    line-height: 20px;
    color: #CCC;
  }
  .com-comment .user-info ul > li .master .txt .extra-function .reply i {
    cursor: pointer;
  }
  .com-comment .user-info ul > li .master .txt .extra-function .reply i:nth-child(1) {
    width: 20px;
    background-size: 100% 100%;
  }
  .com-comment .user-info ul > li .master .txt .reply-mail {
    width: 798px;
    height: 88px;
    border: 1px solid #E5E5E5;
    position: relative;
  }
  .com-comment .user-info ul > li .master .txt .reply-mail textarea {
    width: 708px;
    height: 88px;
    outline: medium;
    font-size: 16px;
    color: #333;
    padding: 10px;
    box-sizing: border-box;
  }
  .com-comment .user-info ul > li .master .txt .reply-mail .btn {
    width: 90px;
    height: 88px;
    position: absolute;
    top: 0;
    right: 0;
    background-image: linear-gradient(-225deg, #4BAFFF 0%, #3657FF 100%);
    font-size: 18px;
    color: #FFF;
    line-height: 88px;
    text-align: center;
    cursor: pointer;
  }
  .com-comment .observe {
    width: 860px;
    height: 60px;
    background: #F2F2F2;
    font-size: 16px;
    color: #999;
    line-height: 60px;
    text-align: center;
    margin: 30px 0 60px 0;
  }
  .com-comment .observe span {
    cursor: pointer;
  }
}
