/*
 * @Author: zhaohongyun 
 * @Date: 2018-10-16 17:45:53 
    所有页面为了做响应式布局，
    需要在不同宽度下下显示不同的布局，
    请使用@media eg:@media screen and (max-width:1335px)
*/
html,
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
img {
  display: block;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul,
ol,
li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
q:before,
q:after {
  content: '';
}
abbr,
acronym {
  border: 0;
  font-variant: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-size: 100%;
}
legend {
  color: #000;
}
input[type='button'],
input[type='submit'],
input[type='reset'] {
  -webkit-appearance: none;
}
body {
  min-width: 800px;
  font: normal 12px/1.5 PingFangSC-Regular, Microsoft Yahei, tahoma, sans-serif;
}
strong,
b {
  font-weight: bold;
}
textarea,
input {
  resize: none;
  outline: none;
}
a {
  text-decoration: none;
  outline: none;
}
a:hover {
  text-decoration: none;
}
h1 {
  font-size: 20px;
}
h2 {
  font-size: 16px;
}
h3 {
  font-size: 14px;
}
h4,
h5,
h6 {
  font-size: 12px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.cf:before,
.cf:after {
  content: '';
  display: table;
}
.cf:after {
  clear: both;
}
.cf {
  zoom: 1;
}
.hidden {
  display: none;
}
.iframe-blank {
  border: none;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: 0px;
  opacity: 0;
  background: #fff;
  filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
  position: absolute;
  display: block;
}
.plt-clt .g-header,
.plt-clt .g-miniHeader,
.plt-clt .g-search,
.plt-clt .g-sitemap,
.plt-clt .g-copyright {
  clear: both;
  display: none;
}
.blk {
  margin-left: auto;
  margin-right: auto;
}
.alCenter {
  text-align: center;
}
.mt10 {
  margin-top: 10px;
}
.c-2nd {
  color: #9a9a9a;
}
* {
  box-sizing: content-box;
}
.pointer {
  cursor: pointer;
}
.abs {
  position: absolute;
}
.rel {
  position: relative;
}
.mr2 {
  margin-right: 2px;
}
.ml3 {
  margin-left: 3px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.mt17 {
  margin-top: 17px;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.one-line {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.two-line {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
}
.three-line {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 3;
}
.main-wraper {
  position: absolute;
  width: 100%;
  color: #666666;
}
.main-wraper .group:hover .arrow {
  display: block;
}
@media screen and (max-width: 1680px) {
  .transparencyBtnBox {
    width: 1160px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
  }
}
@media screen and (min-width: 1680px) {
  .transparencyBtnBox {
    width: 1640px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
  }
}
/*版心*/
.w10 {
  width: 10%;
}
.w15 {
  width: 15%;
}
.w20 {
  width: 20%;
}
.w30 {
  width: 30%;
}
.tb {
  display: table;
  vertical-align: middle;
}
.tbc {
  display: table-cell;
  vertical-align: middle;
}
/* base 三种尺寸 */
.detail .wPlay {
  background-color: #1E1E1E;
  padding-top: 60px;
}
.detail .site_container {
  margin-bottom: 60px;
}
.player_float_layer {
  position: fixed!important;
  z-index: 1000;
  bottom: 0;
  width: 480px!important;
  height: 270px!important;
  cursor: move;
}
.player_float_layer .player_float_layer_tip {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  color: #fff;
  font-size: 15px;
  height: 30px;
  line-height: 30px;
  background-color: rgba(0, 0, 0, 0.3);
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
.player_float_layer .player_float_layer_tip .player_float_layer_tip_txt {
  padding-left: 10px;
}
.player_float_layer .player_float_layer_tip .player_float_layer_close {
  cursor: pointer;
  display: inline-block;
  width: 30px;
  height: 30px;
  position: absolute;
  right: 0;
  top: 0px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
.player_float_layer_tip_hover {
  display: none;
}
#wPlayer.player_float_layer:hover .mini-control.none {
  cursor: pointer;
  display: block;
}
#wPlayer.player_float_layer:hover .player_float_layer_tip_hover {
  display: block;
  z-index: 10000;
}
#wPlayer.player_float_layer:hover .player_float_layer_tip_hover .player_float_layer_close:hover {
  cursor: pointer;
  display: inline-block;
  width: 30px;
  height: 30px;
  position: absolute;
  right: 0;
  top: 0px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
@media screen and (max-width: 1680px) {
  .player {
    width: 1160px;
    margin: 0 auto;
    padding: 0 20px;
    font-size: 22px;
    background-color: #1E1E1E;
  }
  .player .playcont {
    width: 100%;
  }
  .player .playcont .episode {
    width: 270px;
    height: 550px;
  }
  .player .playcont .webPlay {
    height: 500px;
    background-color: #ccc;
  }
  .player .playcont .webPlay .bulletScreen {
    color: #999;
    background: #262626;
    padding: 0 20px;
    height: 50px;
    line-height: 50px;
  }
  .player .playcont .webPlay .bulletScreen > .right {
    margin-left: 510px;
    position: relative;
    font-size: 12px;
    color: #999;
  }
  .player .playcont .webPlay .bulletScreen > .right .sendBulletScreen {
    width: 100%;
    height: 30px;
    position: relative;
    line-height: 30px;
    display: inline-block;
    border-radius: 15px;
    background-color: #393939;
  }
  .player .playcont .webPlay .bulletScreen > .right .sendBulletScreen input {
    height: inherit;
    width: 60%;
    outline: none;
    border-radius: 15px;
    color: #fff;
    background-color: #393939;
    padding-left: 10px;
    box-sizing: border-box;
  }
  .player .playcont .webPlay .bulletScreen > .right .sendBulletScreen input::-webkit-input-placeholder {
    /* placeholder颜色  */
    color: #656565;
    /* placeholder字体大小  */
    font-size: 12px;
    /* placeholder位置  */
  }
  .player .playcont .webPlay .bulletScreen > .right .sendBulletScreen .sendBtn {
    width: 50px;
    height: 30px;
    color: #B3B3B3;
    position: absolute;
    text-align: center;
    line-height: 30px;
    top: 0;
    right: 0px;
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
  }
  .player .playcont .webPlay .bulletScreen > .right .sendBulletScreen .icons .font,
  .player .playcont .webPlay .bulletScreen > .right .sendBulletScreen .icons .face {
    width: 30px;
    height: 30px;
    position: absolute;
    top: 0;
    right: 50px;
  }
  .player .playcont .webPlay .bulletScreen > .right .sendBulletScreen .icons .face {
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
  }
  .player .playcont .webPlay .bulletScreen > .right .sendBulletScreen .icons .font {
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
    right: 80px;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting {
    position: absolute;
    width: 320px;
    height: 80px;
    padding: 20px;
    background-color: #000;
    top: -110px;
    right: 50px;
    font-size: 0;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > div {
    display: inline-block;
    position: relative;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > div .title {
    position: absolute;
    top: -18px;
    font-size: 12px;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left {
    border-right: 1px solid #333;
    height: inherit;
    width: 129px;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content {
    padding: 34px 22px 0 8px;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content li {
    width: 10px;
    height: 10px;
    padding: 2px;
    border-radius: 50%;
    float: left;
    margin: 0 6px 6px 0;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content li:nth-child(5n+5) {
    margin-right: 0;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content .red {
    background-color: #FF4040;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content .blue {
    background-color: #3099FF;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content .yellow {
    background-color: #FFB411;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content .green {
    background-color: #01D40E;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content .darkYellow {
    background-color: #FF9500;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content .skyblue {
    background-color: #00D0D7;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content .redPink {
    background-color: #F40097;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content .pink {
    background-color: #FF79D0;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content .qing {
    background-color: #00660D;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content .white {
    background-color: #fff;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content li:after {
    width: 10px;
    height: 10px;
    background-color: red;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .left .content .bgc {
    border: 2px solid rgba(255, 255, 255, 0.4);
    width: 8px;
    height: 8px;
  }
  .player .playcont .webPlay .bulletScreen > .right .bulletScreenSetting > .right {
    width: calc(-5%);
    height: inherit;
    margin-left: 20px;
  }
  .player .playcont .webPlay .play {
    height: 100%;
    background-color: #000;
    position: relative;
  }
  .player .playcont .webPlay .play .episodeControl {
    width: 10px;
    height: 60px;
    position: absolute;
    right: -10px;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
  }
  .player .playcont .webPlay .play #wPlayer {
    width: 100%;
    height: 100%;
    position: absolute;
  }
  .container_inner {
    width: 1160px;
    margin: 20px auto 0;
    font-size: 0;
  }
  .container_inner > div {
    display: inline-block;
  }
  .container_inner .container_main {
    width: 860px;
    padding-right: 30px;
  }
  .container_inner .container_side {
    font-size: 12px;
    width: 270px;
    vertical-align: top;
  }
}
