/* base 三种尺寸 */
.twoLine {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
}
.moduleCont::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 5px;
  /*高宽分别对应横竖滚动条的尺寸*/
  -webkit-box-shadow: inset 1px 0 1px rgba(0, 0, 0, 0.2);
}
.moduleCont::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 3px;
  background: #4d4d4d;
  height: 120px;
}
.moduleCont::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  background: rgba(0, 0, 0, 0.1);
}
.vipBadge {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
