.pagination-wrapper .pagination {
  text-align: center;
}
.pagination-wrapper .pagination li {
  display: inline-block;
  min-width: 10px;
  height: 30px;
  line-height: 30px;
  padding: 0 10px;
  text-align: center;
  font-size: 14px;
  color: #343434;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.pagination-wrapper .pagination .active {
  color: #FFFFFF;
  background: #007AFF;
  border-radius: 15px;
}
.pagination-wrapper .pagination .prevMore,
.pagination-wrapper .pagination .nextMore {
  font-weight: bolder;
}
.pagination-wrapper .pagination .disabled {
  opacity: .5;
  cursor: default;
}
