.login-wrapper {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 4860;
}
.login-wrapper .login-close-btn {
  right: 0;
  position: absolute;
  margin-top: 5px;
  margin-right: 10px;
  cursor: pointer;
}
.login-wrapper .login-close-btn::before {
  content: "\2716";
  width: 30px;
  height: 30px;
  font-size: 26px;
  color: #f39610;
}
.login-wrapper .login-iframe {
  width: 460px;
  height: 420px;
  overflow: hidden;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}
