/* base 三种尺寸 */
html,
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
img {
  display: block;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul,
ol,
li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
q:before,
q:after {
  content: '';
}
abbr,
acronym {
  border: 0;
  font-variant: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-size: 100%;
}
legend {
  color: #000;
}
input[type='button'],
input[type='submit'],
input[type='reset'] {
  -webkit-appearance: none;
}
body {
  min-width: 800px;
  font: normal 12px/1.5 PingFangSC-Regular, Microsoft Yahei, tahoma, sans-serif;
}
strong,
b {
  font-weight: bold;
}
textarea,
input {
  resize: none;
  outline: none;
}
a {
  text-decoration: none;
  outline: none;
}
a:hover {
  text-decoration: none;
}
h1 {
  font-size: 20px;
}
h2 {
  font-size: 16px;
}
h3 {
  font-size: 14px;
}
h4,
h5,
h6 {
  font-size: 12px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.cf:before,
.cf:after {
  content: '';
  display: table;
}
.cf:after {
  clear: both;
}
.cf {
  zoom: 1;
}
.hidden {
  display: none;
}
.iframe-blank {
  border: none;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: 0px;
  opacity: 0;
  background: #fff;
  filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
  position: absolute;
  display: block;
}
.plt-clt .g-header,
.plt-clt .g-miniHeader,
.plt-clt .g-search,
.plt-clt .g-sitemap,
.plt-clt .g-copyright {
  clear: both;
  display: none;
}
.blk {
  margin-left: auto;
  margin-right: auto;
}
.alCenter {
  text-align: center;
}
.mt10 {
  margin-top: 10px;
}
.c-2nd {
  color: #9a9a9a;
}
* {
  box-sizing: content-box;
}
.pointer {
  cursor: pointer;
}
.abs {
  position: absolute;
}
.rel {
  position: relative;
}
.mr2 {
  margin-right: 2px;
}
.ml3 {
  margin-left: 3px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.mt17 {
  margin-top: 17px;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.one-line {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.two-line {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
}
.three-line {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 3;
}
.search-dropdown {
  position: relative;
  background: #FFFFFF;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.1);
  width: 300px;
  margin-left: 20px;
}
.search-dropdown .history-result-box {
  padding: 0 15px;
  border-bottom: 1px solid  #F3F3F3;
}
.search-dropdown .history-result-box .h-r-content .h-list {
  width: 100%;
  font-size: 0;
}
.search-dropdown .history-result-box .h-r-content .h-list .h-item {
  display: inline-block;
  width: 114px;
  height: 30px;
  padding: 0 8px;
  line-height: 30px;
  margin-bottom: 10px;
  background: #F7F7F7;
  text-align: center;
  font-size: 12px;
  color: #333333;
  cursor: pointer;
}
.search-dropdown .history-result-box .h-r-content .h-list .h-item:nth-child(2n) {
  margin-left: 10px;
}
.search-dropdown .history-result-box .h-r-content .h-list .h-item:hover {
  color: #007AFF;
  background: #EDEDED;
}
.search-dropdown .search-result-box .s-r-content .s-r-list {
  width: 100%;
  font-size: 0px;
}
.search-dropdown .search-result-box .s-r-content .s-r-list .s-r-item {
  width: 270px;
  height: 30px;
  line-height: 30px;
  padding: 0 15px;
  color: #333333;
  cursor: pointer;
}
.search-dropdown .search-result-box .s-r-content .s-r-list .s-r-item .s-r-item-content {
  width: 100%;
  text-align: left;
  font-size: 0;
  height: 30px;
  line-height: 30px;
  position: relative;
  display: block;
}
.search-dropdown .search-result-box .s-r-content .s-r-list .s-r-item .s-r-item-content:hover {
  color: #007AFF;
}
.search-dropdown .search-result-box .s-r-content .s-r-list .s-r-item .s-r-item-content .s-r-item-lab {
  position: relative;
  display: inline-block;
  width: 18px;
  height: 18px;
  line-height: 18px;
  border-radius: 10px;
  color: #FFFFFF;
  font-size: 12px;
  font-style: normal;
  text-align: center;
  vertical-align: middle;
}
.search-dropdown .search-result-box .s-r-content .s-r-list .s-r-item .s-r-item-content .s-r-item-lab.color-y1 {
  background-color: #FF5757;
}
.search-dropdown .search-result-box .s-r-content .s-r-list .s-r-item .s-r-item-content .s-r-item-lab.color-y2 {
  background-color: #FF7D26;
}
.search-dropdown .search-result-box .s-r-content .s-r-list .s-r-item .s-r-item-content .s-r-item-lab.color-y3 {
  background-color: #FFD54F;
}
.search-dropdown .search-result-box .s-r-content .s-r-list .s-r-item .s-r-item-content .s-r-item-txt {
  display: inline-block;
  position: relative;
  font-size: 12px;
  margin-left: 6px;
  vertical-align: middle;
}
.search-dropdown .search-result-box .s-r-content .s-r-list .s-r-item:hover {
  background: #EDEDED;
}
.search-dropdown .search-result-box .s-r {
  width: 270px;
  padding: 0 15px;
}
.search-dropdown .r-title {
  position: relative;
  width: 100%;
  font-size: 12px;
  height: 40px;
  line-height: 40px;
  cursor: default;
}
.search-dropdown .r-title .r-txt-1 {
  float: left;
  color: #808080;
}
.search-dropdown .r-title .r-txt-2 {
  float: right;
  color: #B4B4B4;
  cursor: pointer;
}
.search-dropdown .r-title .r-txt-2:hover {
  color: #007AFF;
}
.search-dropdown .tip-key-box {
  position: relative;
  padding: 10px 0;
}
.search-dropdown .tip-key-box .t-k-content {
  position: relative;
}
.search-dropdown .tip-key-box .t-k-content .t-k-list {
  width: 100%;
}
.search-dropdown .tip-key-box .t-k-content .t-k-list .tip-key-item {
  display: block;
  position: relative;
  width: 270px;
  padding: 0 15px;
  text-align: left;
  font-size: 12px;
  height: 30px;
  line-height: 30px;
  color: #333333;
  cursor: pointer;
}
.search-dropdown .tip-key-box .t-k-content .t-k-list .tip-key-item:hover {
  color: #007AFF;
  background: #EDEDED;
}
@media screen and (min-width: 1680px) {
  .search-dropdown {
    width: 400px;
    cursor: default;
    margin: 0 auto;
    left: -40px;
  }
  .search-dropdown .history-result-box {
    padding: 0 20px;
  }
  .search-dropdown .history-result-box .h-r-content .h-list .h-item {
    width: 159px;
    padding: 0 8px;
  }
  .search-dropdown .search-result-box .s-r-content .s-r-list {
    width: 100%;
    font-size: 0px;
  }
  .search-dropdown .search-result-box .s-r-content .s-r-list .s-r-item {
    width: 360px;
    padding: 0 20px;
  }
  .search-dropdown .search-result-box .s-r {
    width: 360px;
    padding: 0 20px;
  }
  .search-dropdown .tip-key-box .t-k-content .t-k-list .tip-key-item {
    width: 360px;
    padding: 0 20px;
  }
}
