.openMemberModalToPayWrap {
  padding: 20px 0;
  width: 760px;
  margin: auto;
  height: 368px;
  box-sizing: border-box;
}
.openMemberModalToPayWrap .adv {
  cursor: pointer;
  height: 100%;
  position: relative;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.openMemberModalToPayWrap h5 {
  font-size: 16px;
  color: #4D4D4D;
  font-weight: bold;
}
.openMemberModalToPayWrap .payActionWrap {
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.1);
  margin-top: 15px;
  background: #F8F7F7;
}
.openMemberModalToPayWrap .payActionWrap:after {
  clear: both;
  display: block;
  content: '';
}
.openMemberModalToPayWrap .payActionWrap .left {
  float: left;
  width: 160px;
}
.openMemberModalToPayWrap .payActionWrap .right {
  float: right;
  width: 600px;
  background: white;
}
.openMemberModalToPayWrap .payActionWrap .slotDetail {
  position: relative;
}
.openMemberModalToPayWrap .payActionWrap .slotDetail > * {
  display: inline-block;
  vertical-align: middle;
}
.openMemberModalToPayWrap .payActionWrap .slotDetail i {
  width: 20px;
  height: 20px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
.openMemberModalToPayWrap .payActionWrap .slotDetail i.QRCode,
.openMemberModalToPayWrap .payActionWrap .slotDetail i.fixQRCode {
  background-image: url(data:image/png;base64,);
}
.openMemberModalToPayWrap .payActionWrap .slotDetail i.HF {
  background-image: url(data:image/png;base64,);
}
.openMemberModalToPayWrap .payActionWrap .slotDetail .title {
  font-size: 16px;
  color: #4D4D4D;
  margin-left: 5px;
}
