/* base 三种尺寸 */
.left_img_right_text {
  padding-left: 10px;
  margin-bottom: 30px;
}
.left_img_right_text .titles {
  width: 100%;
  padding: 0;
  margin: 0 auto;
  font-size: 18px;
  color: #323232;
  height: 50px;
}
.left_img_right_text .titles .leftName {
  bottom: auto;
  top: 10px;
}
.oneline {
  width: 100%;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media screen and (max-width: 1680px) {
  .left_img_right_text .content li {
    width: 100%;
    height: 60px;
    margin-bottom: 10px;
  }
  .left_img_right_text .content li .innerLeft {
    width: 100px;
    height: inherit;
    float: left;
    position: relative;
    cursor: pointer;
  }
  .left_img_right_text .content li .innerLeft .pic {
    width: inherit;
  }
  .left_img_right_text .content li .innerLeft .pic img {
    width: 100px;
    height: 60px;
  }
  .left_img_right_text .content li .innerLeft .badge {
    width: 16px;
    height: 18px;
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
    position: absolute;
    top: 0;
    left: 4px;
    color: #fff;
    text-align: center;
  }
  .left_img_right_text .content li .innerLeft .actBadge {
    background: url(data:image/png;base64,) no-repeat;
  }
  .left_img_right_text .content li .innerRight {
    margin-left: 100px;
    padding: 0 0 10px 8px;
    font-size: 14px;
    color: #323232;
  }
  .left_img_right_text .content li .innerRight .videoName {
    cursor: pointer;
  }
  .left_img_right_text .content li .innerRight .videoName:hover {
    color: #007AFF;
  }
  .left_img_right_text .content li .innerRight .videoActors {
    width: 100%;
    font-size: 12px;
    color: #999999;
  }
}
@media screen and (min-width: 1680px) {
  .left_img_right_text .content li {
    width: 100%;
    height: 80px;
    margin-bottom: 10px;
  }
  .left_img_right_text .content li .innerLeft {
    width: 134px;
    height: inherit;
    float: left;
    position: relative;
  }
  .left_img_right_text .content li .innerLeft .pic {
    width: inherit;
  }
  .left_img_right_text .content li .innerLeft .pic img {
    width: 134px;
    height: 80px;
  }
  .left_img_right_text .content li .innerLeft .badge {
    width: 16px;
    height: 18px;
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
    position: absolute;
    top: 0;
    left: 4px;
    color: #fff;
    text-align: center;
  }
  .left_img_right_text .content li .innerLeft .actBadge {
    background: url(data:image/png;base64,) no-repeat;
  }
  .left_img_right_text .content li .innerRight {
    margin-left: 134px;
    padding: 0 0 20px 8px;
    font-size: 16px;
    color: #323232;
  }
  .left_img_right_text .content li .innerRight .videoName {
    cursor: pointer;
  }
  .left_img_right_text .content li .innerRight .videoName:hover {
    color: #007AFF;
  }
  .left_img_right_text .content li .innerRight .videoActors {
    width: 100%;
    font-size: 12px;
    color: #999999;
  }
}
