.openMemberModalUserInfo {
  height: 80px;
  box-sizing: border-box;
  display: table;
  padding: 0 60px 0 25px;
  max-width: 100%;
  width: 100%;
}
.openMemberModalUserInfo > * {
  display: table-cell;
  vertical-align: middle;
}
.openMemberModalUserInfo .imgWrap {
  width: 50px;
}
.openMemberModalUserInfo .imgWrap .img {
  height: 50px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%;
  background-image: url(data:image/png;base64,);
}
.openMemberModalUserInfo .info {
  padding-left: 18px;
}
.openMemberModalUserInfo .info .name {
  font-size: 16px;
  color: #C19E00;
  margin-bottom: 3px;
}
.openMemberModalUserInfo .info .memberInfo {
  font-size: 14px;
  color: #CCCCCC;
}
