.playerFooter {
  font-size: 16px;
  color: #CDCDCD;
  padding: 16px 0;
  position: relative;
}
.playerFooter .mod_intro {
  margin-right: 270px;
}
.playerFooter .mod_intro .video_base .video_title {
  display: inline-block;
  width: auto;
  max-width: 638px;
  vertical-align: middle;
}
.playerFooter .mod_intro .video_base .video_title a {
  color: #CDCDCD;
}
.playerFooter .mod_intro .video_base .video_title a:hover {
  color: #007AFF;
}
.playerFooter .mod_intro .video_base .video_title span {
  font-size: 12px;
  color: #666;
  margin: 0 10px 0 8px;
}
.playerFooter .mod_intro .video_base .video_score {
  color: #007AFF;
  display: inline-block;
  vertical-align: middle;
  padding: 0 12px 0 20px;
}
.playerFooter .mod_intro .video_base .video_score .units {
  font-size: 25px;
}
.playerFooter .mod_intro .video_base .video_score .decimal {
  font-size: 20px;
}
.playerFooter .mod_intro .video_info_wrap {
  font-size: 0;
  margin-top: 9px;
}
.playerFooter .mod_intro .video_info_wrap .video_info .video_tags a {
  color: #999;
  font-size: 12px;
  background-color: #333;
  display: inline-block;
  margin-right: 6px;
  padding: 0 6px;
}
.playerFooter .mod_intro .video_info_wrap .video_info .video_tags a:last-child {
  margin-right: 0px;
}
.playerFooter .mod_mood {
  position: absolute;
  top: 14px;
  right: 286px;
}
.playerFooter .mod_mood > div {
  display: inline-block;
}
.playerFooter .mod_mood .mood_like,
.playerFooter .mod_mood .mood_dislike {
  width: 36px;
  height: 36px;
  background-size: contain;
}
.playerFooter .mod_mood .mood_like {
  background: url(data:image/png;base64,) no-repeat;
  vertical-align: middle;
  position: relative;
}
.playerFooter .mod_mood .mood_like .like {
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.playerFooter .mod_mood .mood_dislike {
  background: url(data:image/png;base64,) no-repeat;
  vertical-align: middle;
  position: relative;
}
.playerFooter .mod_mood .mood_dislike .dislike {
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.playerFooter .mod_mood .pk {
  position: relative;
}
.playerFooter .mod_mood .pk .pk_line {
  width: 60px;
  height: 2px;
  border-radius: 1px;
  background: #D8D8D8;
  vertical-align: middle;
}
.playerFooter .mod_mood .pk .pk_line .act_line {
  height: 2px;
  background: #007aff;
}
.playerFooter .mod_mood .pk .rate {
  font-size: 12px;
  color: #007AFF;
  position: absolute;
  left: 50%;
  top: 6px;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.playerFooter .mod_mood p {
  position: absolute;
  bottom: -20px;
  font-size: 14px;
}
.playerFooter .mod_ad {
  position: absolute;
  top: 0;
  right: 0;
  display: none;
}
.oneline {
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
