.openMemberModalTabWrap {
  width: 100%;
  box-sizing: border-box;
}
.openMemberModalTabWrap .tab {
  cursor: pointer;
}
.openMemberModalTabWrap.type_1 {
  height: 74px;
}
.openMemberModalTabWrap.type_1:after {
  display: block;
  clear: both;
  content: "";
}
.openMemberModalTabWrap.type_1 .tab {
  float: left;
  padding: 15px 20px;
  box-sizing: border-box;
  width: 50%;
  position: relative;
}
.openMemberModalTabWrap.type_1 .tab.active:before {
  background: #C19E00;
  display: block;
  content: "";
  height: 2px;
  width: 340px;
  position: absolute;
  bottom: 20px;
  left: 20px;
}
.openMemberModalTabWrap.type_2 {
  position: relative;
  height: 102px;
  box-sizing: border-box;
}
.openMemberModalTabWrap.type_2 .swiper-container {
  padding: 10px;
  margin: 0 20px;
  position: relative;
}
.openMemberModalTabWrap.type_2 .swiper-container::after {
  display: block;
  content: "";
  clear: both;
}
.openMemberModalTabWrap.type_2 .swiper-container .slideItem {
  position: relative;
  height: 80px;
  width: 160px;
  border: 1px solid #ECECEC;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  display: table;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.openMemberModalTabWrap.type_2 .swiper-container .slideItem.wider {
  width: 240px;
}
.openMemberModalTabWrap.type_2 .swiper-container .slideItem .tab {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
}
.openMemberModalTabWrap.type_2 .swiper-container .slideItem.active {
  border-color: #C19E00;
}
.openMemberModalTabWrap.type_2 .swiper-container .slideItem.active:after {
  display: block;
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  width: 36px;
  height: 36px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}
.openMemberModalTabWrap.type_2 .swiper-container .slideItem.recommend:before {
  display: block;
  content: "";
  position: absolute;
  right: -6px;
  top: -8px;
  width: 50px;
  height: 22px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}
.openMemberModalTabWrap.type_2 .action {
  display: block;
  height: 100%;
  position: absolute;
  width: 16px;
  top: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  cursor: pointer;
  z-index: 2;
}
.openMemberModalTabWrap.type_2 .action.next {
  background-image: url(data:image/png;base64,);
  right: 2px;
}
.openMemberModalTabWrap.type_2 .action.pre {
  background-image: url(data:image/png;base64,);
  left: 2px;
}
.openMemberModalTabWrap.type_3 .tab {
  width: 100%;
  height: 60px;
  box-sizing: border-box;
  padding: 19px 25px;
}
.openMemberModalTabWrap.type_3 .tab.active {
  background: white;
}
