/* base 三种尺寸 */
.live-info {
  color: #fff;
  margin-top: 20px;
}
.live-info .title {
  font-size: 0;
}
.live-info .title > div {
  display: inline-block;
  width: 100%;
  text-align: center;
  cursor: pointer;
}
.live-info .title > div:hover {
  color: #007AFF;
}
.live-info .content .program ul li {
  font-size: 12px;
  padding: 4px 0;
  position: relative;
  cursor: pointer;
}
.live-info .content .program ul li > div {
  display: inline-block;
  vertical-align: middle;
}
.live-info .content .program ul li .playStatus {
  position: absolute;
  right: 0px;
  color: #007AFF;
}
.live-info .content .program ul li .startTime {
  padding: 0 13px 0  20px;
}
.live-info .content .program ul li:hover {
  color: #007AFF;
  background: #1a1a1a;
}
.live-info .content .tv-station {
  background: #222;
}
.live-info .content .tv-station .tv-data ul {
  font-size: 0;
}
.live-info .content .tv-station .tv-data ul li {
  display: inline-block;
  color: #666;
  cursor: pointer;
}
.live-info .content .tv-station .tv-content ul li {
  padding: 10px 0;
}
.live-info .content .tv-station .tv-content ul li .tv-now {
  font-size: 0;
  color: #666;
}
.live-info .content .tv-station .tv-content ul li .tv-now > span {
  display: inline-block;
  font-size: 12px;
  vertical-align: middle;
}
.appointment {
  color: #B3B3B3!important;
}
.liveActStyle {
  color: #007AFF;
  border-bottom: 1px solid;
}
.actLiveCont {
  background: #1a1a1a;
}
.actLiveTxt {
  color: #007AFF !important;
}
.actTv {
  color: #ffffff!important;
  background-color: #007AFF;
  border-radius: 13px;
}
.onelineLive {
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media screen and (max-width: 1680px) {
  .live-info {
    padding: 25px 0 0;
  }
  .live-info .title {
    font-size: 0;
    background: #3a3a3a;
    height: 30px;
    line-height: 30px;
  }
  .live-info .title > div {
    font-size: 12px;
  }
  .live-info .content .program,
  .live-info .content .tv-station {
    height: 362px;
    padding-top: 10px;
  }
  .live-info .content .program ul {
    padding: 0 15px;
  }
  .live-info .content .program ul li .programName {
    max-width: 125px;
  }
  .live-info .content .program ul li .playStatus {
    padding: 0 5px;
    top: 6px;
  }
  .live-info .content .tv-station .tv-data {
    padding: 14px 15px;
    border-bottom: 1px solid #404040;
  }
  .live-info .content .tv-station .tv-data ul li {
    font-size: 12px;
    padding: 4px 10px;
  }
  .live-info .content .tv-station .tv-content {
    font-size: 14px;
    color: #FFFFFF;
    height: 285px;
  }
  .live-info .content .tv-station .tv-content ul {
    padding: 0 15px;
  }
  .live-info .content .tv-station .tv-content ul li {
    padding: 10px 0;
  }
  .live-info .content .tv-station .tv-content ul li .tv-now .name {
    margin-left: 6px;
    width: 200px;
  }
  .live-info .content .tv-station .tv-content ul li .tv-name {
    cursor: pointer;
  }
}
@media screen and (min-width: 1680px) {
  .live-info {
    padding: 25px 0 0;
  }
  .live-info .title {
    font-size: 0;
    background: #3a3a3a;
    height: 30px;
    line-height: 30px;
  }
  .live-info .title > div {
    font-size: 14px;
  }
  .live-info .content .program,
  .live-info .content .tv-station {
    height: 564px;
    padding-top: 10px;
  }
  .live-info .content .program ul {
    padding: 0 30px;
  }
  .live-info .content .program ul li .programName {
    max-width: 175px;
  }
  .live-info .content .program ul li .playStatus {
    padding: 0 5px;
    top: 6px;
  }
  .live-info .content .program ul li .startTime {
    padding-left: 0 13px 0  30px;
  }
  .live-info .content .tv-station .tv-data {
    padding: 14px 30px;
    border-bottom: 1px solid #404040;
  }
  .live-info .content .tv-station .tv-data ul li {
    font-size: 12px;
    padding: 4px 10px;
  }
  .live-info .content .tv-station .tv-content {
    font-size: 14px;
    color: #FFFFFF;
    height: 513px;
  }
  .live-info .content .tv-station .tv-content ul li {
    padding: 10px 30px;
  }
  .live-info .content .tv-station .tv-content ul li .tv-now .name {
    margin-left: 6px;
    width: 200px;
  }
  .live-info .content .tv-station .tv-content ul li .tv-name {
    cursor: pointer;
  }
}
