.txt-img-wrapper {
  position: relative;
}
.txt-img-wrapper .title {
  font-size: 20px;
  color: #323232;
}
.txt-img-wrapper .txt-list {
  margin-top: 30px;
}
.txt-img-wrapper .txt-list .item {
  font-size: 14px;
  color: #656565;
  display: inline-block;
  margin-right: 60px;
  cursor: pointer;
}
.txt-img-wrapper .txt-list .item:hover {
  color: #007AFF;
}
.txt-img-wrapper .txt-list .item:hover .mobile {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 24px;
  height: 45px;
  background-position: -108px -107px;
}
.txt-img-wrapper .txt-list .item:hover .i_window {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 34px;
  height: 34px;
  background-position: -38px -176px;
}
.txt-img-wrapper .txt-list .item:hover .mac {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 33px;
  height: 40px;
  background-position: -44px -66px;
}
.txt-img-wrapper .txt-list .item:hover .ipad {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 30px;
  height: 39px;
  background-position: -79px -66px;
}
.txt-img-wrapper .txt-list .item .bg {
  display: block;
  margin: 0 auto;
}
.txt-img-wrapper .txt-list .item .mobile {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 24px;
  height: 45px;
  background-position: -109px -154px;
}
.txt-img-wrapper .txt-list .item .i_window {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 34px;
  height: 34px;
  background-position: -38px -212px;
}
.txt-img-wrapper .txt-list .item .mac {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 33px;
  height: 40px;
  background-position: -74px -176px;
}
.txt-img-wrapper .txt-list .item .ipad {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 30px;
  height: 39px;
  background-position: -76px -108px;
}
.txt-img-wrapper .txt-list .item .txt {
  display: inline-block;
  margin-top: 10px;
}
