.mr16 {
  margin-right: 16px;
}
.mt5 {
  margin-top: 5px;
}
.icon-status {
  width: 12px;
  height: 12px;
  float: left;
}
.login-info-box {
  font-size: 12px;
  color: #323232;
  width: 300px;
  border-top: 4px solid #007aff;
  line-height: 16px;
  vertical-align: middle;
}
.login-info-box .reorder {
  background-image: linear-gradient(-45deg, #958468 0%, #dcd2aa 100%);
  border-radius: 10px;
  padding-left: 14px;
  padding-right: 14px;
  height: 20px;
  color: #ffffff;
  text-align: center;
  margin-right: 8px;
  background-color: #958468;
}
.login-info-box .openmore {
  border: 1px solid #c6bd85;
  color: #c6bd85;
  border-radius: 10px;
  width: 100px;
  height: 18px;
  line-height: 18px;
  text-align: center;
  margin-right: 10px;
}
.login-info-box .bottom-info {
  padding-top: 12px;
  padding-bottom: 12px;
  height: 20px;
  line-height: 20px;
  vertical-align: middle;
  color: #b3b3b3;
  position: relative;
}
.login-info-box .btn-group {
  position: absolute;
  top: 13px;
  right: 0;
}
.login-info-box .main-container {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.login-info-box .line {
  width: 100%;
  height: 1px;
  background: #e6e6e6;
}
.login-info-box .op-btn {
  color: #9a9a9a;
}
.login-info-box .info {
  height: 16px;
}
.login-info-box .user-info {
  padding-bottom: 15px;
}
.login-info-box .user-info .l {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  overflow: hidden;
  float: left;
  margin-left: 10px;
}
.login-info-box .user-info .l img {
  width: 100%;
  height: auto;
  border-radius: 50%;
}
.login-info-box .user-info .r {
  margin-left: 50px;
}
.tri {
  border-width: 8px;
  border-color: transparent transparent #007aff transparent;
  border-style: solid;
  top: -18px;
  right: 24px;
  width: 0;
  height: 0;
  position: absolute;
}
