.openMemberModalToPayDetailWrap {
  padding: 50px;
  box-sizing: border-box;
  width: 100%;
  text-align: center;
}
.openMemberModalToPayDetailWrap > * {
  display: inline-block;
  vertical-align: middle;
  margin: 0 20px;
}
.openMemberModalToPayDetailWrap .QRCode {
  width: 160px;
}
.openMemberModalToPayDetailWrap .QRCode.fixQRCode i {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}
.openMemberModalToPayDetailWrap .QRCode i {
  display: block;
  height: 160px;
  width: 100%;
  position: relative;
}
.openMemberModalToPayDetailWrap .QRCode > p {
  font-size: 16px;
  color: #4D4D4D;
  text-align: center;
  margin-top: 5px;
}
.openMemberModalToPayDetailWrap .HF {
  width: 200px;
}
.openMemberModalToPayDetailWrap .HF .telWrap {
  display: table;
  width: 100%;
  height: 94px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
  margin-bottom: 15px;
}
.openMemberModalToPayDetailWrap .HF .telWrap .tel {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.openMemberModalToPayDetailWrap .HF .telWrap .tel > p {
  font-size: 16px;
  color: #333333;
}
.openMemberModalToPayDetailWrap .HF .telWrap .tel > div {
  font-size: 12px;
  color: #B2B2B2;
  margin-top: 5px;
}
.openMemberModalToPayDetailWrap .HF .checkStatus > span {
  font-size: 12px;
  color: #E4CF6E;
  margin-left: 3px;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}
.openMemberModalToPayDetailWrap .HF .payBtn {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
  box-shadow: 0 1px 4px 0 rgba(129, 114, 88, 0.3);
  font-size: 12px;
  color: #FFFFFF;
  width: 150px;
  cursor: pointer;
  height: 30px;
  text-align: center;
  line-height: 30px;
  margin: 15px auto;
  border-radius: 15px;
}
.openMemberModalToPayDetailWrap .agreement {
  width: 200px;
  text-align: left;
}
.openMemberModalToPayDetailWrap .agreement h5 {
  font-size: 14px;
  color: #666666;
  margin-bottom: 10px;
}
.openMemberModalToPayDetailWrap .agreement ul {
  color: #666666;
  margin-bottom: 6px;
}
.openMemberModalToPayDetailWrap .agreement .agreementLink {
  cursor: pointer;
  margin-bottom: 5px;
  font-size: 12px;
  color: #B2B2B2;
}
