 尾部组件 文字列表
*/
.txt-col-wrapper {
  position: relative;
}
.txt-col-wrapper .title {
  font-size: 20px;
  color: #323232;
}
.txt-col-wrapper .txt-list {
  margin-top: 30px;
}
.txt-col-wrapper .txt-list .item {
  font-size: 14px;
  color: #656565;
  margin-bottom: 20px;
  display: block;
}
.txt-col-wrapper .txt-list .item:hover {
  color: #007AFF;
}
