.pModal {
  position: fixed;
  height: 100%;
  width: 100%;
  display: table;
  z-index: 5;
  top: 0;
  left: 0;
  /*二级确认框*/
}
.pModal .bg {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.3);
}
.pModal .contentWrap {
  display: table-cell;
  vertical-align: middle;
  position: relative;
  z-index: 4;
}
.pModal .contentWrap .content {
  background-color: white;
  border: 1px solid #979797;
  border-radius: 8px;
  margin: auto;
  position: relative;
  padding: 0px 10px 15px;
  width: 500px;
  box-sizing: border-box;
}
.pModal .contentWrap .content .close {
  cursor: pointer;
  position: absolute;
  top: 12px;
  right: 10px;
  width: 30px;
  height: 30px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
}
.pModal .contentWrap .content .detailWrap > h5 {
  height: 54px;
  line-height: 54px;
  text-align: left;
  text-indent: 2em;
  font-size: 18px;
  color: #333333;
  letter-spacing: 0.22px;
  border-bottom: 1px solid #EEEEEE;
}
.pModal.type_1 .contentWrap .content .detailWrap > h5 {
  text-align: center;
}
.pModal.type_1 .contentWrap .content .detailWrap .detail {
  display: table;
  height: 150px;
  width: 100%;
}
.pModal.type_1 .contentWrap .content .detailWrap .detail .detailItem {
  vertical-align: middle;
  display: table-cell;
}
.pModal.type_1 .contentWrap .content .detailWrap .btnWrap {
  margin-top: 20px;
  text-align: center;
  padding-bottom: 30px;
}
.pModal.type_1 .contentWrap .content .detailWrap .btnWrap > * {
  display: inline-block;
  margin: 0 25px;
  width: 90px;
  text-align: center;
  height: 30px;
  line-height: 30px;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}
.pModal.type_1 .contentWrap .content .detailWrap .btnWrap > *:first-child {
  background-color: #0092DC;
}
.pModal.type_1 .contentWrap .content .detailWrap .btnWrap > *:last-child {
  background-color: #999;
}
.pModal.type_2 .content {
  padding: 0;
  border-radius: 0;
  width: 800px;
  border: none;
}
.pModal.type_2 .content .close {
  top: 24px!important;
  right: 15px!important;
  width: 30px!important;
  height: 30px!important;
}
