.program_des h1 {
  font-size: 20px;
  color: #323232;
  font-weight: 700;
}
.program_des .program_info {
  font-size: 12px;
  color: #989898;
}
.program_des .program_info div {
  display: inline-block;
}
.program_des .program_info .program_role {
  margin: 10px 0;
}
.program_des .program_info .program_role .program_director {
  margin-right: 10px;
}
.program_des .program_info .program_detail {
  overflow: hidden;
  position: relative;
}
.program_des .program_info .program_detail .detail_btn {
  width: 61px;
  height: 16px;
  line-height: 16px;
  background-size: contain;
  position: absolute;
  bottom: 0;
  right: 0;
  font-size: 12px;
  text-align: center;
}
.program_des .program_info .program_detail .downArrow {
  background: url(data:image/png;base64,) no-repeat;
}
.program_des .program_info .program_detail .downArrow:hover {
  background: url(data:image/png;base64,) no-repeat;
}
.program_des .program_info .program_detail .upArrow {
  background: url(data:image/png;base64,) no-repeat;
}
.program_des .program_info .program_detail .upArrow:hover {
  background: url(data:image/png;base64,) no-repeat;
}
.program_des .program_info .program_detail .detail_btn:hover {
  color: #007AFF;
  cursor: pointer;
}
