/* base 三种尺寸 */
@media screen and (max-width: 1680px) {
  .left-img-right-text-player .episodeSideCommonClass {
    color: #fff;
    font-size: 0;
    margin-bottom: 10px;
  }
  .left-img-right-text-player .episodeSideCommonClass .left {
    position: relative;
    width: 100px;
    height: 60px;
    font-size: 12px;
    display: inline-block;
    vertical-align: top;
  }
  .left-img-right-text-player .episodeSideCommonClass .left img {
    width: inherit;
    height: inherit;
    cursor: pointer;
  }
  .left-img-right-text-player .episodeSideCommonClass .left > div {
    position: absolute;
  }
  .left-img-right-text-player .episodeSideCommonClass .left .duration {
    bottom: 0;
    left: 0;
    padding: 0 2px;
    margin-left: 2px;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .left-img-right-text-player .episodeSideCommonClass .left .badge {
    top: 3px;
    right: 3px;
    width: 20px;
    height: 13px;
  }
  .left-img-right-text-player .episodeSideCommonClass .right {
    display: inline-block;
    font-size: 12px;
    color: #999;
    vertical-align: top;
    margin-left: 10px;
    width: 130px;
  }
  .left-img-right-text-player .episodeSideCommonClass .right .name {
    font-size: 14px;
    line-height: 20px;
    height: 40px;
    color: #B3B3B3;
    width: 100%;
    overflow: hidden;
    cursor: pointer;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    /*! autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
  }
  .left-img-right-text-player .episodeSideCommonClass .right .itemTags a {
    color: #999;
  }
  .left-img-right-text-player .episodeSideCommonClass .right .times {
    margin-top: 4px;
    font-size: 0;
  }
  .left-img-right-text-player .episodeSideCommonClass .right .times > span {
    vertical-align: middle;
    font-size: 12px;
  }
  .left-img-right-text-player .episodeSideCommonClass .right .times .timeIcon {
    display: inline-block;
    border-left: 5px solid #999;
    border-top: 5px solid transparent;
    border-right: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-radius: 3px;
  }
  .left-img-right-text-player .episodeSideCommonClass .right:hover .name {
    color: #007AFF;
  }
}
