.memberTypeTabWrap {
  box-shadow: 0px -5px 20px #f2f2f2;
}
.memberTypeTabWrap .memberTypeTab {
  text-align: center;
  padding: 10px 20px;
}
.memberTypeTabWrap .memberTypeTab i {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  margin-right: 3px;
}
.memberTypeTabWrap .memberTypeTab i.video {
  background-image: url(data:image/png;base64,);
}
.memberTypeTabWrap .memberTypeTab i.sport {
  background-image: url(data:image/png;base64,);
}
.memberTypeTabWrap .memberTypeTab > * {
  display: inline-block;
  vertical-align: middle;
}
.memberTypeTabWrap .memberTypeTab span.title {
  font-size: 16px;
  color: #323232;
}
.memberTypeTabWrap .memberTypeTab span.subTitle {
  font-size: 12px;
  color: #B2B2B2;
}
.memberTypeTabWrap .memberTypeTab.active span.title {
  color: #C19E00;
}
.memberTypeTabWrap .memberTypeTab.active span.subTitle {
  color: #E4CF6E;
}
.memberTypeTabWrap .memberTypeTab.active i.video {
  background-image: url(data:image/png;base64,);
}
.memberTypeTabWrap .memberTypeTab.active i.sport {
  background-image: url(data:image/png;base64,);
}
