/* base 三种尺寸 */
.fixed-bg {
  background-image: linear-gradient(-212deg, #8A0000 0%, #001D52 100%) !important;
}
.display {
  display: inline-block;
}
.color {
  color: #CDCDCD !important;
}
.text-color {
  color: #666 !important;
}
.text-color:hover {
  color: #007AFF !important;
}
.hand-close {
  background: url(data:image/png;base64,) no-repeat !important;
}
.default {
  cursor: default !important;
}
@media screen and (max-width: 1640px) {
  .channel-children {
    background-image: linear-gradient(-212deg, #8A0000 0%, #001D52 100%);
    padding: 10px 0 30px 0;
  }
  .channel-children ul {
    width: 1160px;
    margin: 0 auto;
    overflow: hidden;
  }
  .channel-children ul li {
    width: 180px;
    height: 48px;
    text-align: center;
    margin-right: 16px;
    margin-bottom: 20px;
    float: left;
    cursor: pointer;
  }
  .channel-children ul li span {
    height: 48px;
    line-height: 48px;
    font-size: 16px;
    color: #FFFFFF;
  }
  .channel-children ul li:nth-child(6n) {
    margin-right: 0;
  }
}
@media screen and (min-width: 1640px) {
  .channel-children {
    background-image: linear-gradient(-212deg, #8A0000 0%, #001D52 100%);
    padding: 10px 0 30px 0;
  }
  .channel-children ul {
    width: 1640px;
    margin: 0 auto;
    overflow: hidden;
  }
  .channel-children ul li {
    width: 260px;
    height: 48px;
    text-align: center;
    margin-right: 16px;
    margin-bottom: 20px;
    float: left;
    cursor: pointer;
  }
  .channel-children ul li span {
    height: 48px;
    line-height: 48px;
    font-size: 16px;
    color: #FFFFFF;
  }
  .channel-children ul li:nth-child(6n) {
    margin-right: 0;
  }
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #ccc;
  font-size: 14px;
}
input:-moz-placeholder,
textarea:-moz-placeholder {
  color: #ccc;
  font-size: 14px;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #ccc;
  font-size: 14px;
}
.header-wrapper {
  position: absolute;
  width: 100%;
  min-width: 1200px;
  height: 60px;
  background: rgba(0, 0, 0, 0.6);
  margin: 0 auto;
  left: 0;
  top: 0;
  z-index: 100;
}
.header-wrapper.rel {
  position: relative;
}
.header-wrapper.bg-black {
  background: rgba(0, 0, 0, 0);
}
.header-wrapper.bg-black .header-inner .logo .header-nav .header-nav-item {
  color: #CDCDCD;
}
.header-wrapper.fixed {
  position: fixed;
}
.header-wrapper.bg-white {
  background: #ffffff;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
}
.header-wrapper.bg-white .header-inner .logo .header-nav {
  display: inline-block;
}
.header-wrapper.bg-white .header-inner .search-wrapper .search-box {
  background-color: rgba(216, 216, 216, 0.3);
}
.header-wrapper.bg-white .header-inner .side-wrapper .side-item .icon-item .text {
  position: relative;
  font-size: 12px;
  color: #666666;
  text-shadow: none;
}
.header-wrapper .header-inner {
  width: 1160px;
  padding: 0 20px;
  position: relative;
  margin: 0 auto;
}
.header-wrapper .header-inner::before {
  content: " ";
  display: table;
  clear: both;
}
.header-wrapper .header-inner::after {
  content: " ";
  display: table;
  clear: both;
}
.header-wrapper .header-inner .logo {
  width: 400px;
  height: 100%;
  vertical-align: middle;
  float: left;
}
.header-wrapper .header-inner .logo .icon {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 144px;
  height: 26px;
  background-position: 0 0;
  cursor: pointer;
  margin-top: 17px;
  position: relative;
  display: inline-block;
}
.header-wrapper .header-inner .logo .header-nav {
  font-size: 14px;
  text-align: center;
  margin: 0 auto;
  height: 60px;
  line-height: 60px;
  position: relative;
  vertical-align: top;
  margin-left: 50px;
}
.header-wrapper .header-inner .logo .header-nav .header-nav-item {
  margin-right: 10px;
  color: #fff;
  opacity: 0.7;
}
.header-wrapper .header-inner .logo .header-nav .header-nav-item .nav-all {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}
.header-wrapper .header-inner .logo .header-nav .header-nav-item .nav-all .all-icon .icon-all {
  display: block;
  width: 34px;
  height: 34px;
  background: url(data:image/png;base64,) no-repeat;
  cursor: pointer;
}
.header-wrapper .header-inner .logo .header-nav .header-nav-item:hover {
  color: #007AFF;
}
.header-wrapper .header-inner .search-wrapper {
  position: absolute;
  width: 35%;
  margin-left: 400px;
  overflow: hidden;
}
.header-wrapper .header-inner .search-wrapper .search-box {
  position: relative;
  font-size: 0;
  width: 400px;
  height: 36px;
  text-align: center;
  margin: 0 auto;
  margin-top: 12px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  cursor: default;
}
.header-wrapper .header-inner .search-wrapper .search-box .box-left {
  display: inline-block;
  width: 220px;
  height: 36px;
  margin-right: 10px;
}
.header-wrapper .header-inner .search-wrapper .search-box .box-left .search-box-input {
  position: relative;
  width: 100%;
  height: 100%;
  font-size: 14px;
  padding-left: 10px;
  color: #666666;
  padding: 0;
  line-height: 36px;
  border: 0 none;
  outline: 0;
  background: 0 0;
  vertical-align: middle;
}
.header-wrapper .header-inner .search-wrapper .search-box .box-mid {
  display: inline-block;
  width: 50px;
  height: 16px;
  font-size: 12px;
  vertical-align: middle;
  cursor: pointer;
}
.header-wrapper .header-inner .search-wrapper .search-box .box-mid .hot-icon {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 8px;
  height: 8px;
  background-position: -74px -235px;
  cursor: pointer;
  display: block;
  float: left;
  margin-top: 6px;
}
.header-wrapper .header-inner .search-wrapper .search-box .box-mid .hot-txt {
  color: #FF4C4C;
}
.header-wrapper .header-inner .search-wrapper .search-box .box-right {
  font-size: 14px;
  vertical-align: middle;
  float: right;
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 90px;
  height: 36px;
  background-position: 0px -28px;
  cursor: pointer;
}
.header-wrapper .header-inner .search-wrapper .search-box .box-right .search-txt {
  color: #fff;
  font-size: 14px;
  line-height: 36px;
  padding-left: 10px;
}
.header-wrapper .header-inner .side-wrapper {
  position: absolute;
  height: 60px;
  right: 0;
}
.header-wrapper .header-inner .side-wrapper::before {
  content: " ";
  display: table;
  clear: both;
}
.header-wrapper .header-inner .side-wrapper::after {
  content: " ";
  display: table;
  clear: both;
}
.header-wrapper .header-inner .side-wrapper .side-item {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}
.header-wrapper .header-inner .side-wrapper .side-item .icon-item {
  position: relative;
  text-align: center;
  margin: 0 auto;
  height: 60px;
  color: #fff;
  opacity: 0.7;
  width: 60px;
  margin-top: 10px;
}
.header-wrapper .header-inner .side-wrapper .side-item .icon-item .icon {
  position: relative;
  display: block;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 5px;
}
.header-wrapper .header-inner .side-wrapper .side-item .icon-item .text {
  position: relative;
  font-size: 12px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}
.header-wrapper .header-inner .side-wrapper .header-vip .icon-item .icon {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 21px;
  height: 18px;
  background-position: 0px -33px;
  cursor: pointer;
  background-size: 128px 128px;
}
.header-wrapper .header-inner .side-wrapper .header-vip .icon-item:hover {
  color: #007AFF;
}
.header-wrapper .header-inner .side-wrapper .header-vip .icon-item:hover .icon {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 21px;
  height: 18px;
  background-position: 0px -52px;
  cursor: pointer;
  background-size: 128px 128px;
}
.header-wrapper .header-inner .side-wrapper .header-vip .icon-item:hover .text {
  text-shadow: null;
  color: #007AFF;
}
.header-wrapper .header-inner .side-wrapper .header-history .icon-item .icon {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 18px;
  height: 18px;
  background-position: 0px -107px;
  cursor: pointer;
  background-size: 128px 128px;
}
.header-wrapper .header-inner .side-wrapper .header-history .icon-item:hover {
  color: #007AFF;
}
.header-wrapper .header-inner .side-wrapper .header-history .icon-item:hover .icon {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 18px;
  height: 18px;
  background-position: 0px -71px;
  cursor: pointer;
  background-size: 128px 128px;
}
.header-wrapper .header-inner .side-wrapper .header-history .icon-item:hover .text {
  text-shadow: null;
  color: #007AFF;
}
.header-wrapper .header-inner .side-wrapper .header-download .icon-item .icon {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 18px;
  height: 16px;
  background-position: -19px -71px;
  cursor: pointer;
  background-size: 128px 128px;
  margin-bottom: 7px;
}
.header-wrapper .header-inner .side-wrapper .header-download .icon-item:hover {
  color: #007AFF;
}
.header-wrapper .header-inner .side-wrapper .header-download .icon-item:hover .icon {
  background: url(../assets/index_sprites.png) no-repeat;
  background-size: 256px 256px;
  width: 18px;
  height: 16px;
  background-position: 0px -90px;
  cursor: pointer;
  background-size: 128px 128px;
}
.header-wrapper .header-inner .side-wrapper .header-download .icon-item:hover .text {
  text-shadow: null;
  color: #007AFF;
}
.header-wrapper .header-inner .side-wrapper .header-user .icon-item {
  margin-left: 30px;
}
.header-wrapper .header-inner .side-wrapper .header-user .icon-item img {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.header-wrapper .header-inner .side-wrapper .history-popbox {
  position: absolute;
  z-index: 1001;
  background-color: #ffffff;
  right: -110px;
  top: 60px;
}
.header-wrapper .header-inner .side-wrapper .login-popbox {
  position: absolute;
  z-index: 1001;
  background-color: #ffffff;
  right: 6px;
  top: 60px;
}
@media screen and (min-width: 1680px) {
  .header-wrapper .header-inner {
    width: 1640px;
  }
  .header-wrapper .header-inner .search-wrapper {
    margin-left: 400px;
    width: 50%;
  }
  .header-wrapper .header-inner .search-wrapper .search-box {
    width: 500px;
  }
  .header-wrapper .header-inner .search-wrapper .search-box .box-left {
    width: 320px;
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
