.QRCodeWrap {
  display: block;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  top: 0;
  left: 0;
}
.QRCodeWrap > .QRCode {
  height: 100%;
  width: 100%;
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  left: 0;
  top: 0;
}
.QRCodeWrap > .QRCode:before {
  position: absolute;
  display: block;
  content: "";
  width: 16%;
  height: 16%;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
  z-index: 2;
  border-radius: 20%;
  background-color: white;
}
.QRCodeWrap > .QRCode .QRCodeDecoWrap {
  position: absolute;
  display: table;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.QRCodeWrap > .QRCode .QRCodeDecoWrap .QRCodeDeco {
  vertical-align: middle;
  display: table-cell;
  max-height: 100%;
  max-width: 100%;
}
.QRCodeWrap > .noneImg {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../assets/loading.gif);
  background-size: 50% 50%;
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}
