 尾部样式
*/
/* base 三种尺寸 */
.footer {
  position: relative;
  width: 100%;
  min-width: 1200px;
  height: 300px;
  background-color: #F6F6F6;
  padding-top: 50px;
  margin-top: 30px;
}
.footer .footer-inner {
  position: relative;
  margin: 0 auto;
  background: rgba(216, 246, 216, 0);
}
.footer .footer-inner::before {
  content: " ";
  display: table;
  clear: both;
}
.footer .footer-inner::after {
  content: " ";
  display: table;
  clear: both;
}
.footer .footer-inner .group {
  position: relative;
  overflow: hidden;
  vertical-align: top;
  display: inline-block;
}
.footer .footer-inner .group .title {
  font-weight: 600;
}
.footer .footer-inner .copyright {
  position: relative;
  margin-top: 30px;
  line-height: 21px;
  font-size: 14px;
  color: #B3B3B3;
  text-align: center;
}
.footer .footer-inner .copyright .txt2 {
  margin-top: 5px;
}
.footer .footer-inner .copyright .txt2 a {
  display: inline-block;
  margin-left: 10px;
  color: #B3B3B3;
}
.footer .footer-inner .copyright .txt2 a:hover {
  color: #007AFF;
}
.footer .footer-inner .copyright .txt2 a .icon_police {
  display: inline-block;
  width: 20px;
  height: 20px;
  background: url(../assets/police.png) no-repeat center center;
  background-size: contain;
  vertical-align: bottom;
}
@media screen and (max-width: 1680px) {
  .footer .footer-inner {
    width: 1160px;
    padding: 0 60px;
    box-sizing: border-box;
  }
  .footer .footer-inner .group .txt-col-wrapper {
    margin-right: 120px;
  }
  .footer .footer-inner .group:nth-child(3) .txt-img-wrapper {
    margin-right: 100px;
  }
  .footer .footer-inner .group:nth-child(4) .txt-col-wrapper {
    margin: 0;
  }
}
@media screen and (min-width: 1680px) {
  .footer .footer-inner {
    width: 1640px;
    padding: 0 100px;
    box-sizing: border-box;
  }
  .footer .footer-inner .group .txt-col-wrapper {
    margin-right: 240px;
  }
  .footer .footer-inner .group:nth-child(3) .txt-img-wrapper {
    margin-right: 200px;
  }
  .footer .footer-inner .group:nth-child(4) .txt-col-wrapper {
    margin: 0;
  }
}
