.pToast {
  position: fixed;
  z-index: 9999999;
  top: 50%;
  left: 50%;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  text-align: center;
  color: white;
  font-size: 16px;
  min-width: 200px;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
