/* base 三种尺寸 */
.oneline {
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.twoline {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
}
.module_share .share {
  display: inline-block;
}
.module_share .share .share_pop {
  display: none;
  width: 260px;
  height: 158px;
  position: absolute;
  top: 35px;
  left: 50%;
  z-index: 10;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.module_share .share .share_pop .pop_header {
  display: inline-block;
  width: 100%;
  height: 38px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  vertical-align: bottom;
}
.module_share .share .share_pop .pop_content {
  width: 210px;
  height: 80px;
  padding: 20px 25px;
  background: #fff;
  display: inline-block;
  font-size: 0;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.35);
}
.module_share .share .share_pop .pop_content .left {
  display: inline-block;
  width: 76px;
  line-height: normal;
  padding: 0 29px 0 0;
  border-right: 1px solid #E5E5E5;
}
.module_share .share .share_pop .pop_content .left .ic_common {
  display: inline-block;
  width: 30px;
  height: 30px;
  margin: 0 12px 12px 0;
  background-color: #fff;
  opacity: 0.8;
}
.module_share .share .share_pop .pop_content .left i:nth-child(2n) {
  margin: 0 0 12px 0;
}
.module_share .share .share_pop .pop_content .left i:nth-child(3) {
  margin: 0 12px 0 0;
}
.module_share .share .share_pop .pop_content .left i:nth-child(4) {
  margin: 0;
}
.module_share .share .share_pop .pop_content .left .ic_sina {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
.module_share .share .share_pop .pop_content .left .ic_qq_space {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
.module_share .share .share_pop .pop_content .left .ic_renren {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
.module_share .share .share_pop .pop_content .left .ic_baidutieba {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
.module_share .share .share_pop .pop_content .left .ic_sina:hover,
.module_share .share .share_pop .pop_content .left .ic_qq_space:hover,
.module_share .share .share_pop .pop_content .left .ic_renren:hover,
.module_share .share .share_pop .pop_content .left .ic_baidutieba:hover {
  opacity: 1;
}
.module_share .share .share_pop .pop_content .right {
  width: 80px;
  height: 80px;
  margin-left: 24px;
  display: inline-block;
  position: relative;
}
.module_share .share:hover .share_pop {
  display: block;
}
.module_share .download {
  position: relative;
  display: inline-block;
}
.module_share .download .download_pop {
  display: none;
  width: 400px;
  height: 158px;
  position: absolute;
  top: 35px;
  left: 50%;
  z-index: 10;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.module_share .download .download_pop .pop_header {
  display: inline-block;
  width: 100%;
  height: 38px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  vertical-align: bottom;
}
.module_share .download .download_pop .pop_content {
  width: 360px;
  height: 90px;
  padding: 14px 20px 16px 20px;
  background: #fff;
  display: inline-block;
  font-size: 0;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.35);
}
.module_share .download .download_pop .pop_content .top {
  display: block;
  font-size: 0;
}
.module_share .download .download_pop .pop_content .top .img {
  display: inline-block;
  font-size: 0;
  vertical-align: top;
}
.module_share .download .download_pop .pop_content .top .img img {
  width: 100px;
  height: 60px;
  display: inline-block;
  vertical-align: top;
}
.module_share .download .download_pop .pop_content .top .img .name {
  margin: 5px 0 0 10px;
  display: inline-block;
  width: 123px;
  height: 32px;
  overflow: hidden;
  vertical-align: top;
  line-height: normal;
  font-size: 12px;
  color: #333333;
}
.module_share .download .download_pop .pop_content .top .download {
  display: inline-block;
  margin: 4px 0 0 19px;
}
.module_share .download .download_pop .pop_content .top .download .btn {
  display: block;
  width: 90px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-radius: 15px;
  color: #007AFF;
  border: 1px solid #007AFF;
  margin: 0 auto;
}
.module_share .download .download_pop .pop_content .top .download .btn:hover {
  background-color: #007AFF;
  color: #fff;
}
.module_share .download .download_pop .pop_content .top .download .tip {
  display: block;
  margin-top: 6px;
  color: #b2b2b2;
  line-height: normal;
}
.module_share .download .download_pop .pop_content .bottom {
  font-size: 12px;
  color: #666666;
}
.module_share .download .download_pop .pop_content .bottom span > i {
  display: inline-block;
  vertical-align: middle;
  width: 26px;
  height: 26px;
}
.module_share .download .download_pop .pop_content .bottom > .tip1 > i {
  background: url(data:image/png;base64,) no-repeat;
  margin-left: 3px;
}
.module_share .download .download_pop .pop_content .bottom > .tip2 > i {
  background: url(data:image/png;base64,) no-repeat;
  margin-left: 10px;
}
.module_share .download:hover .download_pop {
  display: block;
}
.module_share .mobileWatch {
  position: relative;
  display: inline-block;
}
.module_share .mobileWatch .mobile_watch_pop {
  display: none;
  width: 360px;
  height: 228px;
  position: absolute;
  top: 35px;
  left: 50%;
  z-index: 10;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.module_share .mobileWatch .mobile_watch_pop .pop_header {
  display: inline-block;
  width: 100%;
  height: 38px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  vertical-align: bottom;
}
.module_share .mobileWatch .mobile_watch_pop .pop_content {
  width: 340px;
  height: 180px;
  padding: 10px 10px 0;
  background: #fff;
  display: inline-block;
  font-size: 0;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.35);
}
.module_share .mobileWatch .mobile_watch_pop .pop_content .play_tip {
  display: block;
  width: 100%;
  height: 30px;
  line-height: 30px;
  font-size: 0;
  background-color: rgba(0, 122, 255, 0.15);
}
.module_share .mobileWatch .mobile_watch_pop .pop_content .play_tip .name {
  color: #007AFF;
  margin-left: 10px;
  width: 156px;
  display: inline-block;
}
.module_share .mobileWatch .mobile_watch_pop .pop_content .play_tip .progress {
  display: inline-block;
  margin-left: 59px;
  color: #999;
}
.module_share .mobileWatch .mobile_watch_pop .pop_content .pop_qrcode {
  display: block;
  padding: 20px 0 30px;
  width: 340px;
  height: 100px;
  font-size: 0;
}
.module_share .mobileWatch .mobile_watch_pop .pop_content .pop_qrcode .left {
  width: 149px;
  height: inherit;
  display: inline-block;
  border-right: 1px solid  #E5E5E5;
  text-align: center;
}
.module_share .mobileWatch .mobile_watch_pop .pop_content .pop_qrcode .left .qrcode {
  display: block;
  width: 100px;
  height: inherit;
  margin: 0 auto;
  position: relative;
}
.module_share .mobileWatch .mobile_watch_pop .pop_content .pop_qrcode .left .title {
  vertical-align: top;
  line-height: normal;
  font-size: 12px;
  color: #323232;
  display: inline-block;
  margin-top: 4px;
}
.module_share .mobileWatch .mobile_watch_pop .pop_content .pop_qrcode .right {
  display: inline-block;
  width: 190px;
  height: inherit;
  line-height: 0;
}
.module_share .mobileWatch .mobile_watch_pop .pop_content .pop_qrcode .right .common_size {
  display: inline-block;
  width: 138px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  border: 1px solid #007AFF;
  border-radius: 15px;
  margin: 0 0 10px 30px;
  color: #007AFF;
}
.module_share .mobileWatch .mobile_watch_pop .pop_content .pop_qrcode .right .AndroidBtn {
  border: 1px solid #00ED25;
  color: #00ED25;
}
.module_share .mobileWatch:hover .mobile_watch_pop {
  display: block;
}
@media screen and (max-width: 1680px) {
  .left {
    font-size: 0;
  }
  .left span {
    vertical-align: middle;
    font-size: 12px;
  }
  .left .share > i,
  .left .mobileWatch > i,
  .left .download > i {
    width: 20px;
    height: 20px;
    display: inline-block;
    background-size: contain;
    vertical-align: middle;
    margin-left: 16px;
  }
  .left .times {
    width: 109px;
    display: inline-block;
  }
  .left .share {
    position: relative;
    cursor: pointer;
  }
  .left .share > i {
    background: url(data:image/png;base64,) no-repeat;
  }
  .left .download {
    cursor: pointer;
  }
  .left .download > i {
    background: url(data:image/png;base64,) no-repeat;
  }
  .left .mobileWatch {
    cursor: pointer;
  }
  .left .mobileWatch > i {
    background: url(data:image/png;base64,) no-repeat;
  }
  .left .mobileWatch:hover {
    color: #007AFF;
  }
  .left .mobileWatch:hover > i {
    background: url(data:image/png;base64,) no-repeat;
  }
  .left .download:hover {
    color: #007AFF;
  }
  .left .download:hover > i {
    background: url(data:image/png;base64,) no-repeat;
  }
  .left .share:hover {
    color: #007AFF;
  }
  .left .share:hover > i {
    background: url(data:image/png;base64,) no-repeat;
  }
  .left .bulletScreenBtn {
    width: 40px;
    height: 20px;
    cursor: pointer;
    margin-left: 119px;
    display: inline-block;
    background-size: contain;
    vertical-align: middle;
    background: url(data:image/png;base64,) no-repeat;
  }
  .left .set {
    width: 50px;
    height: 50px;
    display: inline-block;
    background-size: contain;
    vertical-align: middle;
    cursor: pointer;
    background: url(data:image/png;base64,) no-repeat;
  }
  .left .set:hover {
    width: 50px;
    height: 50px;
    display: inline-block;
    background-size: contain;
    vertical-align: middle;
    cursor: pointer;
    background: url(data:image/png;base64,) no-repeat;
  }
}
