/* base 三种尺寸 */
.popup_V {
  position: absolute;
  display: inline-block;
  background-color: #fff;
  height: 280px;
  width: 200px;
  top: -36px;
  box-shadow: #666 0 2px 10px 0;
  left: -24.5px;
  z-index: 99;
}
.popup_V .topImg {
  cursor: pointer;
}
.popup_V .programDes {
  font-size: 12px;
  color: #9a9a9a;
  margin: 8px 10px 0;
}
.popup_V .programDes .titleTip {
  color: #4d4d4d;
  display: inline-block;
  vertical-align: middle;
  font-size: 12px;
}
.popup_V .programDes .titleName {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: middle;
  margin-bottom: 4px;
}
.popup_V .programDes .titleName > span {
  display: inline-block;
}
.popup_V .programDes .titleName .name {
  font-size: 16px;
  font-weight: 700;
  color: #333;
  margin-right: 10px;
}
.popup_V .programDes .star {
  position: relative;
  font-size: 0;
}
.popup_V .programDes .star .starList {
  position: absolute;
  font-size: 12px;
  width: 77%;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  /*加宽度width属来兼容部分浏览*/
}
.popup_V .programDes .info {
  position: relative;
  font-size: 0;
}
.popup_V .programDes .info .infoDet {
  position: absolute;
  font-size: 12px;
  top: 0;
  left: 36px;
  max-height: 35px;
  display: -webkit-box;
  vertical-align: top;
  overflow: hidden;
  text-overflow: ellipsis;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
}
.popup_V .actions {
  width: 100%;
  height: 30px;
  position: absolute;
  bottom: 10px;
}
.popup_V .actions div {
  width: 30px;
  height: 30px;
}
.popup_V .actions .hasFavourite {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  margin-right: 50px;
  position: absolute;
  left: 35px;
  cursor: pointer;
}
.popup_V .actions .favourite {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  position: absolute;
  left: 35px;
}
.popup_V .actions .favourite:hover {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  position: absolute;
  right: 35px;
  cursor: pointer;
}
.popup_V .actions .download {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  position: absolute;
  right: 35px;
}
.popup_V .actions .download:hover {
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  position: absolute;
  right: 35px;
  cursor: pointer;
}
.popup_V .actions .line {
  position: absolute;
  width: 1px;
  height: 30px;
  left: 50%;
  background-color: #d8d8d8;
}
@media screen and (max-width: 1680px) {
  .popup_V {
    width: 200px;
    height: 280px;
  }
  .popup_V .topImg .pic img {
    width: 200px;
    height: 140px;
  }
}
@media screen and (min-width: 1680px) {
  .popup_V {
    width: 240px;
    height: 340px;
  }
  .popup_V .topImg .pic img {
    width: 240px;
    height: 168px;
  }
  .popup_V .programDes {
    margin: 12px 10px 0;
  }
  .popup_V .programDes .star .starList {
    max-width: 212px;
  }
  .popup_V .programDes .info {
    margin-top: 4px;
  }
}
