.PCheckBox {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}
.PCheckBox > i {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  height: 20px;
  width: 20px;
  background-image: url(data:image/png;base64,);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.PCheckBox > i.active {
  background-image: url(data:image/png;base64,);
}
.PCheckBox > span {
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  color: #666;
  margin-left: 20px;
  width: 60px;
}
