/* base 三种尺寸 */
.bounce-enter-active {
  -webkit-animation: bounce-in 0.15s;
          animation: bounce-in 0.15s;
}
.bounce-leave-active {
  -webkit-animation: bounce-in 0.05s reverse;
          animation: bounce-in 0.05s reverse;
}
@-webkit-keyframes bounce-in {
  0% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes bounce-in {
  0% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.base-top-img-bottom-text-pop-v {
  position: relative;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V {
  width: 100%;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li {
  display: inline-block;
  vertical-align: top;
  position: relative;
  margin-bottom: 15px;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .topImg {
  position: relative;
  cursor: pointer;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .topImg .score {
  position: absolute;
  right: 0;
  bottom: 0;
  font-size: 12px;
  padding: 4px 8px;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.6);
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .topImg .tip {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 26px;
  height: 16px;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .topImg .updateEps {
  position: absolute;
  bottom: 0;
  left: 0;
  font-size: 12px;
  color: #fff;
  padding: 4px 8px;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.6);
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .bottomDes {
  font-size: 14px;
  color: #323232;
  margin-top: 6px;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .bottomDes .itemTitle {
  width: 100%;
  font-weight: 400;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .bottomDes .itemTitle:hover {
  color: #007AFF;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .bottomDes .itemTitle:hover .subTitle {
  color: #007AFF;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .bottomDes .subTitle {
  font-size: 12px;
  color: #b3b3b3;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .bottomDes .actor {
  font-size: 0;
  color: #B3B3B3;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .bottomDes .actor span {
  display: inline-block;
  font-size: 12px;
  vertical-align: bottom;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V ul .activeStyleHover {
  height: 280px;
  width: 200px;
  position: absolute;
  z-index: 10;
  left: -28px;
  top: -36px;
  box-shadow: #666 0 2px 10px 0;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V .popup {
  width: 100%;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V .default {
  visibility: hidden;
}
.base-top-img-bottom-text-pop-v .topImgBottomText_V .activeStyle {
  visibility: visible;
}
