@media screen and (max-width: 1680px) {
  .base-top-img-bottom-text-pop-v .topImgBottomText_V ul li {
    margin-bottom: 20px;
  }
  .base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .bottomDes {
    width: 100%;
  }
  .base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .bottomDes .actor span:last-child {
    width: 116px;
  }
}
@media screen and (min-width: 1680px) {
  .base-top-img-bottom-text-pop-v .topImgBottomText_V ul li {
    margin-bottom: 20px;
  }
  .base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .bottomDes {
    width: 100%;
  }
  .base-top-img-bottom-text-pop-v .topImgBottomText_V ul li .bottomDes .actor span:last-child {
