.float-btn {
  position: fixed;
  bottom: 40px;
  right: 0;
}
.float-btn .top-btn {
  position: relative;
  width: 52px;
  height: 52px;
  background: url(data:image/png;base64,) no-repeat;
  cursor: pointer;
}
.float-btn .top-btn:hover {
  background: url(data:image/png;base64,) no-repeat;
}
.float-btn .feedback-btn {
  position: relative;
  width: 52px;
  height: 52px;
  background: url(data:image/png;base64,) no-repeat;
  cursor: pointer;
}
.float-btn .feedback-btn:hover {
  background: url(data:image/png;base64,) no-repeat;
}
